import React, { useState } from "react";

import Select from "react-select";

import "./MortgageInterest.css";

const options = [
  { value: "Under 3%", label: "Under 3%" },
  { value: "3%", label: "3%" },
  { value: "3.25%", label: "3.25%" },
  { value: "3.50%", label: "3.50%" },
  { value: "3.75%", label: "3.75%" },
  { value: "4%", label: "4%" },
  { value: "4.25%", label: "4.25%" },
  { value: "4.50%", label: "4.50%" },
  { value: "4.75%", label: "4.75%" },
  { value: "5%", label: "5%" },
  { value: "5.25%", label: "5.25%" },
  { value: "5.50%", label: "5.50%" },
  { value: "5.75%", label: "5.75%" },
  { value: "6%", label: "6%" },
  { value: "6.25%", label: "6.25%" },
  { value: "6.50%", label: "6.50%" },
  { value: "6.75%", label: "6.75%" },
  { value: "7%", label: "7%" },
  { value: "7.25%", label: "7.25%" },
  { value: "7.50%", label: "7.50%" },
  { value: "7.75%", label: "7.75%" },
  { value: "8%", label: "8%" },
  { value: "8.25%", label: "8.25%" },
  { value: "8.50%", label: "8.50%" },
  { value: "8.75%", label: "8.75%" },
  { value: "9%", label: "9%" },
  { value: "9.25%", label: "9.25%" },
  { value: "9.50%", label: "9.50%" },
  { value: "9.75%", label: "9.75%" },
  { value: "10%", label: "10%" },
  { value: "Over 10%", label: "Over 10%" },
  { value: "I don't know", label: "I don't know" },
];

function MortgageInterest({ setCurrentStep, ansMortgageInterest }) {
  const [interestValue, setInterestValue] = useState(ansMortgageInterest);
  const [haveError, setHaveError] = useState(false);

  const nextBtnClick = () => {
    if (interestValue === null) {
      setHaveError(true);
    } else {
      setCurrentStep("MORTGAGE_INTEREST", "HOME_TYPE", interestValue);
    }
  };

  const inputOnChange = (value) => {
    setInterestValue(value);
    setCurrentStep("MORTGAGE_INTEREST", "HOME_TYPE", value);
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            What is your current mortgage interest rate?
          </h2>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <Select
            options={options}
            className="step-input-box"
            placeholder="Select your current interest rate"
            defaultValue={ansMortgageInterest}
            onChange={(value) => {
              inputOnChange(value);
            }}
          />
          {haveError && (
            <div className="error-message">Please select a valid value</div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">NEXT →</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "MORTGAGE_INTEREST",
              "REMAINING_BALANCE",
              ansMortgageInterest
            );
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default MortgageInterest;
