import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./HowUseHome.css";

function HowUseHome({ setCurrentStep, ansHowUseHome, qOneAns, ansHomeType }) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {qOneAns === 0
              ? "How will you use your new home?"
              : "How are you using this home?"}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansHowUseHome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "HOW_USE_HOME",
                qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
                0
              );
            }}
          >
            {ansHowUseHome === 0 && <AiFillCheckCircle />} Primary Residence
          </div>
          <div
            className={
              ansHowUseHome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "HOW_USE_HOME",
                qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
                1
              );
            }}
          >
            {ansHowUseHome === 1 && <AiFillCheckCircle />} Secondary / Vacation
            Home
          </div>
          <div
            className={
              ansHowUseHome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "HOW_USE_HOME",
                qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
                2
              );
            }}
          >
            {ansHowUseHome === 2 && <AiFillCheckCircle />} Investment Property
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "HOW_USE_HOME",
              qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
              ansHowUseHome
            );
          }}
        >
          {ansHowUseHome !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "HOW_USE_HOME",
              ansHomeType === 4 ? "OWN_LAND" : "HOME_TYPE",
              ansHowUseHome
            );
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default HowUseHome;
