import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./DownPayment.css";

function DownPayment({ setCurrentStep, ansDownPayment }) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            How much of a down payment would you like to make?
          </h2>
          <h3 className="q-step-sub-text">(An estimate is fine)</h3>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansDownPayment === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 0);
            }}
          >
            {ansDownPayment === 0 && <AiFillCheckCircle />} 0%
          </div>
          <div
            className={
              ansDownPayment === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 2);
            }}
          >
            {ansDownPayment === 2 && <AiFillCheckCircle />} 5% ($20,000 + )
          </div>
          <div
            className={
              ansDownPayment === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 4);
            }}
          >
            {ansDownPayment === 4 && <AiFillCheckCircle />} 15% ($60,000 + )
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansDownPayment === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 1);
            }}
          >
            {ansDownPayment === 1 && <AiFillCheckCircle />} 3.5% ($14,000 + )
          </div>
          <div
            className={
              ansDownPayment === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 3);
            }}
          >
            {ansDownPayment === 3 && <AiFillCheckCircle />} 10% ($40,000 + )
          </div>
          <div
            className={
              ansDownPayment === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 5);
            }}
          >
            {ansDownPayment === 5 && <AiFillCheckCircle />} 20% ($80,000 + )
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansDownPayment === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 6);
            }}
          >
            {ansDownPayment === 6 && <AiFillCheckCircle />} More than 20%
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "DOWN_PAYMENT",
              "CURRENT_EMPLOYMENT",
              ansDownPayment
            );
          }}
        >
          {ansDownPayment !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("DOWN_PAYMENT", "SERVED_MILITARY", ansDownPayment);
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default DownPayment;
