const validateStreetAddress = (streetAddress) => {
  if (
    streetAddress === null ||
    streetAddress === "" ||
    typeof streetAddress === "undefined"
  ) {
    return ["Can not empty", false];
  } else if (streetAddress.length > 20) {
    return ["Should be less than 30 characters", false];
  } else {
    return ["Success", true];
  }
};

export default validateStreetAddress;
