import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./MilitaryBranch.css";

function MilitaryBranch({ setCurrentStep, ansMilitaryBranch, qOneAns }) {
  return (
    <div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            What is your branch of military service?
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansMilitaryBranch === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                0
              );
            }}
          >
            {ansMilitaryBranch === 0 && <AiFillCheckCircle />} Army
          </div>
          <div
            className={
              ansMilitaryBranch === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                2
              );
            }}
          >
            {ansMilitaryBranch === 2 && <AiFillCheckCircle />} Navy
          </div>
          <div
            className={
              ansMilitaryBranch === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                4
              );
            }}
          >
            {ansMilitaryBranch === 4 && <AiFillCheckCircle />} Coast Guard
          </div>
          <div
            className={
              ansMilitaryBranch === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                6
              );
            }}
          >
            {ansMilitaryBranch === 6 && <AiFillCheckCircle />} Military Spouse
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansMilitaryBranch === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                1
              );
            }}
          >
            {ansMilitaryBranch === 1 && <AiFillCheckCircle />} Marine Corps
          </div>
          <div
            className={
              ansMilitaryBranch === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                3
              );
            }}
          >
            {ansMilitaryBranch === 3 && <AiFillCheckCircle />} Air Force
          </div>
          <div
            className={
              ansMilitaryBranch === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                5
              );
            }}
          >
            {ansMilitaryBranch === 5 && <AiFillCheckCircle />} National Guard
          </div>
          <div
            className={
              ansMilitaryBranch === 7
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                7
              );
            }}
          >
            {ansMilitaryBranch === 7 && <AiFillCheckCircle />} Other VA
            Eligibility
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansMilitaryBranch === 8
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "MILITARY_BRANCH",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                8
              );
            }}
          >
            {ansMilitaryBranch === 8 && <AiFillCheckCircle />} No Military
            Experience
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "MILITARY_BRANCH",
              qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
              ansMilitaryBranch
            );
          }}
        >
          {ansMilitaryBranch !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "MILITARY_BRANCH",
              "SERVED_MILITARY",
              ansMilitaryBranch
            );
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default MilitaryBranch;
