import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./OwnHome.css";

function OwnHome({ setCurrentStep, ansOwnHome }) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">Do you currently own a home?</h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansOwnHome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_HOME", "WHEN_PURCHASE", 0);
            }}
          >
            {ansOwnHome === 0 && <AiFillCheckCircle />} Yes, I currently own a
            home
          </div>
          <div
            className={
              ansOwnHome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_HOME", "WHEN_PURCHASE", 1);
            }}
          >
            {ansOwnHome === 1 && <AiFillCheckCircle />} No, I am currently
            renting
          </div>
          <div
            className={
              ansOwnHome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_HOME", "WHEN_PURCHASE", 2);
            }}
          >
            {ansOwnHome === 2 && <AiFillCheckCircle />} No, I have other living
            arrangements
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("OWN_HOME", "WHEN_PURCHASE", ansOwnHome);
          }}
        >
          {ansOwnHome !== null && <div className="step-q-button">NEXT →</div>}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("OWN_HOME", "FIRST_TIME_USE", ansOwnHome);
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default OwnHome;
