import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./SpendNewHome.css";

function SpendNewHome({ setCurrentStep, ansSpendNewHome }) {
  return (
    <div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            How much do you plan to spend on your new home?
          </h2>
          <h3 className="q-step-sub-text">(An estimate is fine)</h3>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansSpendNewHome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 0);
            }}
          >
            {ansSpendNewHome === 0 && <AiFillCheckCircle />} under $50,000
          </div>
          <div
            className={
              ansSpendNewHome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 2);
            }}
          >
            {ansSpendNewHome === 2 && <AiFillCheckCircle />} $100,000 - $150,000
          </div>
          <div
            className={
              ansSpendNewHome === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 4);
            }}
          >
            {ansSpendNewHome === 4 && <AiFillCheckCircle />} $200,000 - $250,000
          </div>
          <div
            className={
              ansSpendNewHome === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 6);
            }}
          >
            {ansSpendNewHome === 6 && <AiFillCheckCircle />} $300,000 - $350,000
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansSpendNewHome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 1);
            }}
          >
            {ansSpendNewHome === 1 && <AiFillCheckCircle />} $50,000 - $100,000
          </div>
          <div
            className={
              ansSpendNewHome === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 3);
            }}
          >
            {ansSpendNewHome === 3 && <AiFillCheckCircle />} $150,000 - $200,000
          </div>
          <div
            className={
              ansSpendNewHome === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 5);
            }}
          >
            {ansSpendNewHome === 5 && <AiFillCheckCircle />} $250,000 - $300,000
          </div>
          <div
            className={
              ansSpendNewHome === 7
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 7);
            }}
          >
            {ansSpendNewHome === 7 && <AiFillCheckCircle />} $350,000 - $400,000
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansSpendNewHome === 8
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 8);
            }}
          >
            {ansSpendNewHome === 8 && <AiFillCheckCircle />} $400,000 or more
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", ansSpendNewHome);
          }}
        >
          {ansSpendNewHome !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("SPEND_NEW_HOME", "STEP_1", ansSpendNewHome);
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default SpendNewHome;
