import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./LoanType.css";

function LoanType({ setCurrentStep, ansLoanType }) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            What type of home loan are you refinancing?
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansLoanType === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 0);
            }}
          >
            {ansLoanType === 0 && <AiFillCheckCircle />} Conventional Loan
          </div>
          <div
            className={
              ansLoanType === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 2);
            }}
          >
            {ansLoanType === 2 && <AiFillCheckCircle />} USDA Loan
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansLoanType === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 1);
            }}
          >
            {ansLoanType === 1 && <AiFillCheckCircle />} VA Loan
          </div>
          <div
            className={
              ansLoanType === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 3);
            }}
          >
            {ansLoanType === 3 && <AiFillCheckCircle />} FHA Loan
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansLoanType === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 4);
            }}
          >
            {ansLoanType === 4 && <AiFillCheckCircle />} I don't know
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("LOAN_TYPE", "LOAN_GOAL", ansLoanType);
          }}
        >
          {ansLoanType !== null && <div className="step-q-button">NEXT →</div>}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("LOAN_TYPE", "STEP_1", ansLoanType);
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default LoanType;
