import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./WhenPurchase.css";

function WhenPurchase({ setCurrentStep, ansWhenPurchase, ansFirstTimeUse }) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            When are you planning to make your home purchase?
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansWhenPurchase === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 0);
            }}
          >
            {ansWhenPurchase === 0 && <AiFillCheckCircle />} Immediately: Signed
            a Purchase Agreement
          </div>
          <div
            className={
              ansWhenPurchase === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 2);
            }}
          >
            {ansWhenPurchase === 2 && <AiFillCheckCircle />} Within 30 Days
          </div>
          <div
            className={
              ansWhenPurchase === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 4);
            }}
          >
            {ansWhenPurchase === 4 && <AiFillCheckCircle />} 3 - 6 Months
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansWhenPurchase === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 1);
            }}
          >
            {ansWhenPurchase === 1 && <AiFillCheckCircle />} ASAP: Found a
            House/Offer Pending
          </div>
          <div
            className={
              ansWhenPurchase === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 3);
            }}
          >
            {ansWhenPurchase === 3 && <AiFillCheckCircle />} 2 - 3 Months
          </div>
          <div
            className={
              ansWhenPurchase === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 5);
            }}
          >
            {ansWhenPurchase === 5 && <AiFillCheckCircle />} 6+ Months
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansWhenPurchase === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 6);
            }}
          >
            {ansWhenPurchase === 6 && <AiFillCheckCircle />} No Time Frame/Still
            Researching Options
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", ansWhenPurchase);
          }}
        >
          {ansWhenPurchase !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "WHEN_PURCHASE",
              ansFirstTimeUse === 0 ? "OWN_HOME" : "FIRST_TIME_USE",
              ansWhenPurchase
            );
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default WhenPurchase;
