import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./CurrentCreditScore.css";

function CurrentCreditScore({
  setCurrentStep,
  ansCurrentCreditScore,
  qOneAns,
}) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            What is your current credit score?
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansCurrentCreditScore === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "CURRENT_CREDIT_SCORE",
                qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
                0
              );
            }}
          >
            {ansCurrentCreditScore === 0 && <AiFillCheckCircle />} Excellent
            (720+)
          </div>
          <div
            className={
              ansCurrentCreditScore === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "CURRENT_CREDIT_SCORE",
                qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
                2
              );
            }}
          >
            {ansCurrentCreditScore === 2 && <AiFillCheckCircle />} Fair
            (660-679)
          </div>
          <div
            className={
              ansCurrentCreditScore === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "CURRENT_CREDIT_SCORE",
                qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
                4
              );
            }}
          >
            {ansCurrentCreditScore === 4 && <AiFillCheckCircle />} Poor
            (580-619)
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansCurrentCreditScore === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "CURRENT_CREDIT_SCORE",
                qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
                1
              );
            }}
          >
            {ansCurrentCreditScore === 1 && <AiFillCheckCircle />} Good
            (680-719)
          </div>
          <div
            className={
              ansCurrentCreditScore === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "CURRENT_CREDIT_SCORE",
                qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
                3
              );
            }}
          >
            {ansCurrentCreditScore === 3 && <AiFillCheckCircle />} Below Average
            (620-659)
          </div>
          <div
            className={
              ansCurrentCreditScore === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "CURRENT_CREDIT_SCORE",
                qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
                5
              );
            }}
          >
            {ansCurrentCreditScore === 5 && <AiFillCheckCircle />} Bad (Below
            580)
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "CURRENT_CREDIT_SCORE",
              qOneAns === 0 ? "WORK_REAL_ESTATE" : "MORTGAGE_PAYMENT",
              ansCurrentCreditScore
            );
          }}
        >
          {ansCurrentCreditScore !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "CURRENT_CREDIT_SCORE",
              "HAVE_BANKRUPTCY",
              ansCurrentCreditScore
            );
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default CurrentCreditScore;
