import React from "react";

import { AiFillCheckCircle } from "react-icons/ai";

import "./AnnualIncome.css";

function AnnualIncome({ setCurrentStep, ansAnnualIncome }) {
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            What is your household gross (before taxes) annual income?
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansAnnualIncome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 0);
            }}
          >
            {ansAnnualIncome === 0 && <AiFillCheckCircle />} Less than $30,000
          </div>
          <div
            className={
              ansAnnualIncome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 2);
            }}
          >
            {ansAnnualIncome === 2 && <AiFillCheckCircle />} $50,000 - $75,000
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansAnnualIncome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 1);
            }}
          >
            {ansAnnualIncome === 1 && <AiFillCheckCircle />} $30,000 - $50,000
          </div>
          <div
            className={
              ansAnnualIncome === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 3);
            }}
          >
            {ansAnnualIncome === 3 && <AiFillCheckCircle />} $75,000 - $100,000
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansAnnualIncome === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 4);
            }}
          >
            {ansAnnualIncome === 4 && <AiFillCheckCircle />} Greater than
            $100,000
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", ansAnnualIncome);
          }}
        >
          {ansAnnualIncome !== null && (
            <div className="step-q-button">NEXT →</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "ANNUAL_INCOME",
              "CURRENT_EMPLOYMENT",
              ansAnnualIncome
            );
          }}
        >
          ← Back
        </p>
      </div>
    </div>
  );
}

export default AnnualIncome;
