import React from "react";

import "./ArticleCard.css";

function ArticleCard({ render_data }) {
  return (
    <div className="article-card">
      <a className="a-1" href={render_data.txt_1_link}>
        {render_data.txt_1}
      </a>
      <hr />
      <img src={render_data.img_src} alt="loan-eligibility.jpg"></img>
      <h4 className="head-text-style mt-3"> {render_data.title}</h4>
      <p className="gray-text-style">{render_data.p_txt}</p>
      <a className="a-2" href={render_data.a_tag_link}>
        {render_data.a_tag_txt}
      </a>
    </div>
  );
}

export default ArticleCard;
