import { Button } from "react-bootstrap";

import "./StartQuoteBtn.css";

function StartQuoteBtn() {
  return (
    <div className="check-req-div">
      <div className="container">
        <div className="row d-flex justify-content-center hero-text mt-5 mb-5">
          <div className="col-sm-8 center mt-5 mb-5">
            <hr className="global-hr-tag" />
            <h2 className="head-text-style mt-4">
              Check Official Loan Requirements
            </h2>
            <p className="gray-text-style">
              Get matched with an FHA lender and check your eligibility for a
              low money down loan.
            </p>
            <Button href="/start" variant="warning">
              Start Your Quote →
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default StartQuoteBtn;
